import simpleRestProvider from 'ra-data-simple-rest';
import { fetchUtils } from 'ra-core';
import { Auth } from 'aws-amplify';
import { CORS_EXPLORE_HEADER } from '../configs/cors';
import { API_ENDPOINT } from 'configs/constants';

const httpClient = async (url: any, options: fetchUtils.Options = {}) => {
  if (!options.headers) {
    options.headers = new Headers({ Accept: 'application/json' });
  }

  const session = await Auth.currentSession();
  const token = await session.getIdToken().getJwtToken();
  options.user = {
    authenticated: true,
    token,
  };
  return fetchUtils.fetchJson(url, options);
};

const restProvider = simpleRestProvider(
  `${API_ENDPOINT}`,
  httpClient,
  CORS_EXPLORE_HEADER
);
const NileRestProvider = {
  ...restProvider,
  fetchData: (url: string) => {
    return httpClient(`${url}`, {
      method: 'GET',
    }).then(({ json }) => ({ data: json }));
  },
  upload: (url: string, params: any) => {
    return httpClient(`${url}`, {
      method: 'PUT',
      body: JSON.stringify(params.data),
    }).then(({ json }) => ({ data: json }));
  },
  uploadToS3: (url: string, params: any) => {
    const { type, body } = params;
    return fetchUtils
      .fetchJson(`${url}`, {
        method: 'PUT',
        body,
        headers: new Headers({
          Accept: 'application/json',
          'Content-Type': type || 'application/json',
        }),
      })
      .then(({ json }) => ({ data: json }));
  },
  generateS3Url: (url: string, params: any) => {
    return httpClient(`${url}`, {
      method: 'POST',
      body: JSON.stringify(params.data),
    }).then(({ json }) => ({ data: json }));
  },
};

export default NileRestProvider;
